import { initialUserState, IUserState } from 'src/store/state/user.state';
// import { UserActions, EUserActions } from 'src/store/actions/user.actions';
import {
  login,
  loginSuccess,
  loginFailure,
  register,
} from 'src/store/actions/user.actions';
import { createReducer, on } from '@ngrx/store';
import { Action } from 'rxjs/internal/scheduler/Action';

const loginUser = createReducer(
  initialUserState,
  on(login, (state) => state),
  on(register, (state) => state)
  // on(getUserSuccess, (state) => {...state, user: action.payload})
  // on(loginSuccess),
  // on(loginFailure)
);

export function reducer(initialUserState, action) {
  console.log('Reducer works!');
  console.log(initialUserState);
  console.log(action);
  return loginUser(initialUserState, action);
}

