import { Injectable } from '@angular/core';
import { Actions, createEffect, ofType, Effect } from '@ngrx/effects';
import { EMPTY, from, of } from 'rxjs';
import { map, mergeMap, catchError, exhaustMap } from 'rxjs/operators';
import { UserService } from 'src/services/user.service';
import {
  login,
  loginFailure,
  loginSuccess,
  register,
  registerSuccess,
  registerFailure,
} from '../actions/user.actions';
import { IUserHttp } from 'src/interfaces/user-http.interface';

@Injectable()
export class UserEffects {
  constructor(private actions$: Actions, private userService: UserService) {}
  login$ = createEffect(
    () =>
      this.actions$.pipe(
        ofType(login),
        exhaustMap(
          (action) =>
            this.userService
              .loginUser(action.email, action.password, action.remember)
              .pipe(
                // map((res) => loginSuccess(res.token))
                map((user) => loginSuccess()),
                catchError((error) => of(loginFailure()))
              )
          // console.log(action)
        )
      )

    // this.userService.loginUser()
  );
  register$ = createEffect(() =>
    this.actions$.pipe(
      ofType(register),
      exhaustMap((action) =>
        this.userService
          .registerUser(
            action.firstName,
            action.lastName,
            action.email,
            action.password
          )
          .pipe(
            map((user) => registerSuccess()),
            catchError((error) => of(registerFailure()))
          )
      )
    )
  );

  // loginUser$ = createEffect(() =>
  //   this.actions$.pipe(
  //     ofType(login),
  //     exhaustMap((action) =>
  //       this.userService.loginUser(action.email, action.password).pipe(
  //         map((res) => console.log({ res })),
  //         catchError((err) => of(console.log({ err })))
  //       )
  //     )
  //   )
  // );
}
